<template>
  <div class="upload-index">
    <div v-if="isImg" :class="[uploadFoces ? 'upload-fo' : '' , 'upload-box']" v-paste="handlePaste" tabindex="-1" @focus="upFoces" @blur="upBlur">
      <p>ctrl+V</p>
      <el-upload
        class="upload-demo"
        ref="upload"
        action="container"
        :on-preview="handlePictureCardPreview"
        :before-upload="beforeAvatarUpload"
        :http-request="uploadFiles"
        :before-remove="beforeRemove"
        :on-remove="handleRemove"
        :limit="num"
        :on-change="handleChange"
        :on-exceed="handleExceed"
        :file-list="imgList"
        drag
        multiple>
        <el-button size="mini" type="primary">{{title}}</el-button>
        <div slot="tip" class="annotation">PS: {{ maxSize }}M</div>
      </el-upload>
    </div>
    <el-upload
      v-else
      class="upload-demo"
      ref="upload"
      action="container"
      :on-preview="handlePictureCardPreview"
      :before-upload="beforeAvatarUpload"
      :http-request="uploadFiles"
      :before-remove="beforeRemove"
      :on-remove="handleRemove"
      :limit="num"
      :on-change="handleChange"
      :on-exceed="handleExceed"
      :file-list="imgList"
      drag
      multiple>
      <el-button size="mini" type="primary">{{title}}</el-button>
      <div slot="tip" class="annotation">PS: pdf, {{ maxSize }}M</div>
    </el-upload>

    <el-dialog append-to-body :visible.sync="dialogVisible">
      <div class="view-img">
        <img :src="dialogImageUrl" alt="">
      </div>
    </el-dialog>

    <div style="display: none;">
      <img ref="qfUploadLogo" class="qf-upload-logo" :src="watermarkImg || '../../assets/qf_logo.png'">
    </div>
  </div>
</template>

<script>
import axios from "axios";
const CancelToken = axios.CancelToken;

// import { uploadsFile } from '@/api/user'
import paste from '../../directive/el-paste'

export default {
  name: 'QfUpload',
  directives: { paste },
  props: {
    isImg: { type: Boolean, default: true }, // 
    title: { type: String, default: '/' },
    imgList: {
      type: Array,
      default: []
    },
    dir: {   // 
      type: String,
      default: 'dev'
    },
    num: {   //  
      type: Number,
      default: 9
    },
    delserver: {   // 
      type: Boolean,
      default: true
    },
    uploadName: {  // 
      //  20  21  22  23 ; 24 
      type: String,
      default: ''
    },
    uploadStatus: {   // 
      default: ''
    },
    private: {   //   default 
      type: Number,
      default: 1
    },
    watermark: {  // 
      type: Boolean,
      default: true
    },
    watermarkImg: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      maxSize: 20,
      fileList: [],
      cancelList: [], // 
      dialogImageUrl: '',
      dialogVisible: false,
      uploadFoces: false,
    }
  },
  created () {
  },
  mounted() {
    this.cancelList = []
  },
  methods: {
    upNumAddSub(n){
      let num = this.upNum + n
      this.$emit('update:upNum', num)
    },
    handlePictureCardPreview(file) {
      if(file.full_path){
        if(this.isImg){
          this.dialogImageUrl = file.full_path;
          this.dialogVisible = true;
        }else{
          this.openBlank(file.full_path)
        }
      }
    },
    handleRemove(file, fileList) {
      // this.fileList = fileList
      for(var i in this.imgList){
        if(file.id == this.imgList[i].id){
          this.imgList.splice(i,1)
        }
      }
      this.$refs.upload.abort(); //
      for(var i in this.cancelList){
        if(this.cancelList[i].uid == file.uid && typeof this.cancelList[i].cancel === `function`){
          this.cancelList[i].cancel()
          this.cancelList.splice(i,1)
        }
      }
      // 
      if(this.delserver && file.id){
        this.$emit('delUploadsFile', file.id)
      }
    },
    beforeRemove(file, fileList) {
      if (file && file.status==="success") {
        return this.$confirm(``)
      }
    },
    handleExceed(files, fileList) {
      this.$message.warning(` ${this.num}  ${files.length}  ${files.length + fileList.length} `)
    },
    beforeAvatarUpload(file) {
      let _this = this
      return new Promise((resolve, reject) => {
        let size = file.size / 1024 / 1024
        let isLtM = size < this.maxSize //  this.maxSize
        let isJPG = (file.type === 'image/jpeg' || file.type === 'image/jpg' || file.type === 'image/png');
        let isPDF = file.type === 'application/pdf'
        if(!isLtM) {
          this.$message.warning(`${this.maxSize}M`)
          reject()
        }
        if(this.isImg){
          if(this.dir == 'contract'){
            if(!isJPG && !isPDF) {
              this.$message.warning('PDF')
              reject()
            }
          }
          let isSize = 1 // 
          if(size > 2 && size <= 5){
            isSize = 0.9
          }else if(size > 5 && size <= 10){
            isSize = 0.8
          }else if(size > 10){
            isSize = 0.7
          }
          if(isJPG && isSize){
            let image = new Image(), resultBlob = '';
            image.src = URL.createObjectURL(file);
            image.onload = () => {
              // blob
              resultBlob = _this.compressUpload(image, file, isSize);
              resolve(resultBlob)
            }
            image.onerror = () => {
              reject()
            }
          }else{
            resolve(file)
          }
        }else{
          if(!isPDF) {
            this.$message.warning('PDF')
            reject()
          }
          resolve(file)
        }
      })
    },
    handleChange(file, fileList) {
      for(var i in this.imgList){
        if(this.imgList[i].uid == file.uid){
          return
        }
      }
      this.imgList.push(file)
    },
    uploadFiles(params, type) {
      let that = this
      this.upNumAddSub(1)
      let name = '', _file = '', uid = '', renameFile = null;
      if(type && type == 'paste'){
        name = params.name || 'image.png'
        _file = params
        uid = params.lastModified || new Date().getTime()
      }else{
        name = params.file.name
        _file = params.file
        uid = params.file.uid
      }
      if(this.dir == 'business_license'){
        let name2 = name.split('.')
        name = ''
        renameFile = new File([_file], '.' + name2[1]);
      }
      //  FormData 
      const formData = new FormData()
      if(renameFile){
        formData.append('file[]', renameFile)
      }else{
        formData.append('file[]', _file)
      }
      formData.append('private', this.private)
      let dirName = process.env.VUE_APP_TITLE === 'stage' ? `${this.dir}_dev` : this.dir
      formData.append('dir', dirName)

      if(this.uploadName){
        formData.append('field', this.uploadName)
        formData.append('val', this.uploadStatus)
      }

      // cancel
      let cancelToken = new CancelToken(function executor(c) {
        that.cancelList.push({
          uid: uid,
          cancel: c
        })
      })

      // 
      let onUploadProgress = progressEvent => {
        let percent = (progressEvent.loaded / progressEvent.total * 100) | 0 //onProgress percent
        params.onProgress({ percent:percent })
      }
      let ny = this.$notify({
        message: `${name}...`,
        type: 'info',
        duration: 0
      })
      this.$emit('uploadsFile', {
        formData,
        cancelToken,
        success: function(res) {
          if(res.data.error_code){
            that.$notify.error(`${name}`)
          }else{
            that.imgList.push(...res.data.data)
          }
          for(var i in that.cancelList){
            if(that.cancelList[i].uid == uid){
              that.cancelList.splice(i,1)
            }
          }
          for(var j in that.imgList){
            if(that.imgList[j].uid == uid){
              that.imgList.splice(j,1)
            }
          }
          ny.close()
          that.upNumAddSub(-1)
        },
        error: function(err) {
          for(var j in that.imgList){
            if(that.imgList[j].uid == uid){
              that.imgList.splice(j,1)
            }
          }
          var iList = that.imgList.slice()
          that.imgList.splice(0)
          that.imgList.push(...iList)
          ny.close()
          that.upNumAddSub(-1)
        }
      })
    },

    /* -canvas */
    compressUpload(image, file, multiple) {
      var logoImg = this.$refs.qfUploadLogo
      let canvas = document.createElement('canvas')
      let ctx = canvas.getContext('2d')
      let initSize = image.src.length
      let { width, height } = image
      canvas.width = width
      canvas.height = height
      ctx.fillRect(0, 0, canvas.width, canvas.height)
      ctx.drawImage(image, 0, 0, width, height)
      
      //  logoImg
      if(this.watermark){
        ctx.rotate(-20 * Math.PI / 180)
        ctx.translate(-width/2, -height/2);
        ctx.font = '18px Vedana'
        ctx.fillStyle = '#000'
        ctx.textAlign = 'center'
        ctx.textBaseline = 'Middle'
        ctx.globalAlpha = 0.15
        //
        for (let i = 0; i < width / 110; i++) {  
          for (let j = 0; j < height / 35; j++) {
            if(i % 2 === j % 2){
              let w = i * 300;
              let h = j * 80
              ctx.drawImage(logoImg, w - 180, h - 30, 93, 36)
              ctx.fillText('', w, h);
            }
          }
        }
      }
      
      // 0.1
      let compressData = canvas.toDataURL('image/jpeg', multiple)

      // base64Blob
      let blobImg = this.dataURLtoFile(compressData, file.name, file.type)
     
      return blobImg
    },
    
    /* base64Blob */
    dataURItoBlob(data) {
      let byteString;
      if(data.split(',')[0].indexOf('base64') >= 0) {
          byteString = atob(data.split(',')[1])
      }else {
          byteString = unescape(data.split(',')[1])
      }
      let mimeString = data
          .split(',')[0]
          .split(':')[1]
          .split(';')[0];
      let ia = new Uint8Array(byteString.length)
      for( let i = 0; i < byteString.length; i += 1) {
          ia[i] = byteString.charCodeAt(i)
      }
      return new Blob([ia], {type: mimeString})
    },
    /** 
     * @method base64file
     * @param {String} dataURL base64(base64data:image/jpeg;base64,/XXXXXXXXXX)
     * @param {String} fileName ()
     * @param {String} fileType  image/jpg
     * @return {Object} file
    */
    dataURLtoFile(dataURL, fileName, fileType) {
      var arr = dataURL.split(','), mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
      while(n--){
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], fileName, {type:fileType || 'image/jpg'});  //file
    },
    // 
    handlePaste(e) {
      let _this = this
      if(this.imgList.length >= this.num){
        this.$message.warning(` ${this.num} `)
        return
      }
      let file = null
      if (e.clipboardData && e.clipboardData.items[0] && e.clipboardData.items[0].type && e.clipboardData.items[0].type.indexOf('image') > -1) { //
        file = e.clipboardData.items[0].getAsFile();
      }else {
        this.$message({
          type: 'warning',
          message: ''
        })
        return;
      }

      let image = new Image(), resultBlob = '';
      image.src = URL.createObjectURL(file);
      image.onload = () => {
        // blob
        resultBlob = _this.compressUpload(image, file, 1);
        _this.uploadFiles(resultBlob, 'paste')
      }
      image.onerror = () => {
        this.$message.error('')
      }

      // _this.uploadFiles(file, 'paste')
    },

    // 
    upFoces() {
      this.uploadFoces = true
    },
    upBlur() {
      this.uploadFoces = false
    }
  }
}
</script>



