<template>
  <div class="upload-index">
    <div v-if="isImg" :class="[uploadFoces ? 'upload-fo' : '' , 'upload-box']" v-paste="handlePaste" tabindex="-1" @focus="upFoces" @blur="upBlur">
      <p>ctrl+V</p>
      <!-- :before-remove="beforeRemove" -->
      <el-upload
        class="upload-demo"
        ref="upload"
        action="container"
        :before-upload="beforeAvatarUpload"
        :http-request="uploadFiles"
        :on-remove="handleRemove"
        :limit="num"
        :on-change="handleChange"
        :on-exceed="handleExceed"
        :file-list="fileList"
        :on-preview="handlePreview"
        drag
        multiple>
        <el-button size="mini" type="primary">{{ title }}</el-button>
        <div slot="tip" class="annotation">PS: {{ maxSize }}M</div>
      </el-upload>
    </div>

     <el-upload
      v-else
      class="upload-demo"
      ref="upload"
      action="container"
      :before-upload="beforeAvatarUpload"
      :http-request="uploadFiles"
      :before-remove="beforeRemove"
      :on-remove="handleRemove"
      :limit="num"
      :on-change="handleChange"
      :on-exceed="handleExceed"
      :file-list="fileList"
      :on-preview="handlePreview"
      drag
      multiple>
      <el-button size="mini" type="primary">{{ title }}</el-button>
      <div slot="tip" class="annotation">PS: pdf, {{ maxSize }}M</div>
    </el-upload>

    <div class="qf-upload-logo">
      <img v-if="watermarkConfig.img" ref="qfUploadLogo" :src="watermarkConfig.img">
      <img v-else ref="qfUploadLogo" src="../../assets/qf_logo.png">
    </div>
  </div>
</template>

<script>
import { openLink } from '../../utils/index'
import paste from '../../directive/el-paste' // paste directive

export default {
  name: 'QfQiniuUpload',
  directives: { paste },
  props: {
    fileType: { type: String, default: '' }, //  jpg|pdf (beforeUpload)
    maxSize: { type: Number, default: 500 }, //  default:500
    title: { type: String, default: '' }, // 
    fileList: { type: Array, default: () => [] },
    dir: { type: String, default: 'dev' }, // 
    num: { type: Number, default: 9 }, //  (0|null )
    upNum: { type: Number, default: 0 }, // ...
    watermark: { type: Boolean, default: true }, // 
    watermarkConfig: { //   { title: '', img:'' }
      type: Object, 
      default: () => ({
        title: '',
        img: ''
      })
    },
    private: { type: Number, default: 1 }, //   default 
    qiniuToken: Function, // token
    qiniuView: Function, // 
    beforeUpload: Function, // 
    successCallback: String, // 
  },
  computed: {
    isImg() { // 
      if(!this.fileType || this.fileType.toLowerCase().indexOf('jpg') !== -1){
        return true
      }
      return false
    }
  },
  data() {
    return {
      uploadFoces: false
    }
  },
  methods: {
    upNumAddSub(n){
      let num = this.upNum + n
      this.$emit('update:upNum', num)
    },
    handleRemove(file) {
      for(var i in this.fileList){
        if(file.id == this.fileList[i].id){
          this.fileList.splice(i,1)
        }
      }
      this.$refs.upload.abort(); //
    },
    beforeRemove(file) {
      if(file && file.status === 'success'){
        return this.$confirm(``)
      }
    },
    handleExceed(files, fileList) {
      this.$message.warning(` ${this.num}  ${files.length}  ${files.length + fileList.length} `)
    },
    beforeAvatarUpload(file) {
      if(this.beforeUpload) {
        this.beforeUpload()
      }else{
        return new Promise((resolve, reject) => {
          let size = file.size / 1024 / 1024
          let isLtM = size < this.maxSize //  this.maxSize
          if(!isLtM) {
            this.$message.warning(`${this.maxSize}M`)
            reject()
          }
          let isJPG = (file.type === 'image/jpeg' || file.type === 'image/jpg' || file.type === 'image/png');
          let isPDF = file.type === 'application/pdf'
          let fileType = this.fileType.toLowerCase()
          if(fileType){
            if(fileType.indexOf('jpg') !== -1 && isJPG){
              resolve(file)
            }else if(fileType.indexOf('pdf') !== -1 && isPDF) {
              resolve(file)
            }
            this.$message.warning(`${this.fileType}`)
            reject()
          }else{
            resolve(file)
          }
        })
      }
    },
    handleChange(file) {
      for(var i in this.fileList){
        if(this.fileList[i].uid == file.uid){
          return
        }
      }
      this.fileList.push(file)
    },
    uploadFiles(params, type) {
      let that = this
      let file = '';
      if(type && type == 'paste'){
        file = params;
      }else{
        file = params.file;
      }
      /* ---------------- & --------------- */
      let isJPG = (file.type === 'image/jpeg' || file.type === 'image/jpg' || file.type === 'image/png');
      if(isJPG){
        let size = file.size / 1024 / 1024
        let isSize = 1 // 
        if(size > 2 && size <= 5){
          isSize = 0.9
        }else if(size > 5 && size <= 10){
          isSize = 0.8
        }else if(size > 10){
          isSize = 0.7
        }
        if(isSize !== 1 || this.watermark){
          let image = new Image();
          image.src = URL.createObjectURL(file);
          image.onload = () => {
            // blob
            file = this.compressUpload(image, file, isSize);
          }
          image.onerror = () => {
            this.$message.error('')
            return false
          }
        }
      }
      /* ---------------- & END --------------- */
      
      this.upNumAddSub(1)
      let name = file.name
      /*
      * config.useCdnDomain:  cdn true or false false
      * config.disableStatisticsReport: false
      * config.region:  null  undefined 
      * config.retryCount: 3
      * config.concurrentRequestLimit: number3
      * config.checkByMD5:  MD5  false
      */
      let config = {
        useCdnDomain: true, 
        concurrentRequestLimit: 1, // 1
        // region: qiniu.region.z0
      };

      /*
      * fname: string.
      * customVars: object {'x:xxx': ''};  3.x
      * mimeType: null || array null 
      *  ["image/png", "image/jpeg", "image/gif"]
      */
      let paramsObj = null
      let putExtra = {
        fname: name,
        customVars: paramsObj,
        // mimeType: ["image/png", "image/jpeg", "image/gif"]
      };

      //
      let notifyName = this.$notify({
        message: `"${name}"...`,
        duration: 0
      });

      // token
      this.qiniuToken({is_private: this.private}).then(res => {
        let qiniuObj = res.data.data
        if(qiniuObj) {
          let key = `${qiniuObj.dir}/${this.dir}/${new Date().getTime()}_${name}`; // 
          
          let observable = qiniu.upload(file, key, qiniuObj.token, putExtra, config)
          observable.subscribe({
            next (res) {
              // console.log('' + res.total.loaded)
              // console.log('' + res.total.size)
              // console.log('0100' + res.total.percent);
              if(params.onProgress){
                params.onProgress({percent: res.total.percent})
              }
            },
            error (err) {
              that.uploadEnd(notifyName, file, false, type)
              that.$notify.error(`"${name}"`)
            },
            complete (res) {
              //
              //key  hash  key
              console.log('', file)
              let uploads = {
                id: file.uid,
                name,
                path: `http://${qiniuObj.domain}/${key}`,
                private: that.private
              }
              if(that.private !== 1){
                uploads.file_path = uploads.path
              }
              that.uploadEnd(notifyName, uploads, true, type)
              that.$notify.success(`"${name}"`)
              
              // 
              that.$emit('successCallback', uploads)
            }
          })

          // let subscription = observable.subscribe()
          // subscription.unsubscribe(); // 
        }
      }).catch(() => {
        this.uploadEnd(notifyName, file, false, type)
      })
    },

    /* -canvas */
    compressUpload(image, file, multiple) {
      var logoImg = this.$refs.qfUploadLogo
      let canvas = document.createElement('canvas')
      let ctx = canvas.getContext('2d')
      let { width, height } = image
      canvas.width = width
      canvas.height = height
      ctx.fillRect(0, 0, canvas.width, canvas.height)
      ctx.drawImage(image, 0, 0, width, height)

      //  logoImg
      if(this.watermark){
        let watermarkText = this.watermarkConfig.title
        let imgW = logoImg.width
        let imgH = logoImg.height
        ctx.rotate(-20 * Math.PI / 180)
        ctx.translate(-width/2, -height/2);
        ctx.font = '18px Vedana'
        ctx.fillStyle = '#000'
        ctx.textAlign = 'center'
        ctx.textBaseline = 'Middle'
        ctx.globalAlpha = 0.15
        //
        for (let i = 0; i < width / 110; i++) {  
          for (let j = 0; j < height / 35; j++) {
            if(i % 2 === j % 2){
              let w = i * 300;
              let h = j * 80
              ctx.drawImage(logoImg, w - 180, h - 30, imgW, imgH)
              ctx.fillText(watermarkText, w, h);
            }
          }
        }
      }
      
      // 0.1
      let compressData = canvas.toDataURL('image/jpeg', multiple)
      //  base64file
      let blobImg = this.dataURLtoFile(compressData, file.name, file.type)
      blobImg.uid = blobImg.uid || file.uid
      return blobImg
    },
    
    /** 
     * @method base64file
     * @param {String} dataURL base64(base64data:image/jpeg;base64,/XXXXXXXXXX)
     * @param {String} fileName ()
     * @param {String} fileType  image/jpg
     * @return {Object} file
    */
    dataURLtoFile(dataURL, fileName, fileType) {
      var arr = dataURL.split(','), mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
      while(n--){
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], fileName, {type: fileType || 'image/jpg'});  //file
    },

    // 
    handlePaste(e) { 
      let _this = this
      let file = null
      if (e.clipboardData && e.clipboardData.items[0] && e.clipboardData.items[0].type && e.clipboardData.items[0].type.indexOf('image') > -1) { //
        file = e.clipboardData.items[0].getAsFile();
      }else {
        this.$message({
          type: 'warning',
          message: ''
        })
        return;
      }
      file.uid = new Date().getTime()
      let image = new Image(), resultBlob = '';
      image.src = URL.createObjectURL(file);
      image.onload = () => {
        // blob
        resultBlob = _this.compressUpload(image, file, 1);
        _this.uploadFiles(resultBlob, 'paste')
      }
      image.onerror = () => {
        this.$message.error('')
      }
    },
    // 
    upFoces() {
      this.uploadFoces = true
    },
    upBlur() {
      this.uploadFoces = false
    },

    // 
    handlePreview(file) {
      if(file.file_path){
        openLink(file.file_path)
      }else{
        this.qiniuView({url: file.path}).then(res => {
          let data = res.data.data
          openLink(data.download_url)
        })
      }
    },

    // 
    uploadEnd(notifyName, file, isSuccess, type) {
      notifyName && notifyName.close()
      this.upNumAddSub(-1)
      if(isSuccess){
        if(type === 'paste'){
          this.fileList.push(file)
          return
        }
        this.fileList.forEach((element, i) => {
          if(element.uid == file.id){
            this.fileList.splice(i, 1, file)
          }
        })
      }else{
        this.fileList.forEach((element, i) => {
          if(element.uid == file.uid){
            this.fileList.splice(i, 1)
          }
        })
      }
    }
  }
}
</script>



